import React, { useEffect } from 'react';
import logo from '../components/media/LifeGold.png';
import fundoCentral from '../components/media/FundoSubtitulo.png';
import porqueGeralPC from '../components/media/PorqueGeralPC.png';
import porqueGeralMobile from '../components/media/PorqueGeralMobile.png';
import '../components/styles.css';
import TelaContato from '../components/TelaContato/TelaContato';

function Sobre() {
    return (
        <div className="Sobre">
            <header className="App-header">
            <img src={logo} className="App-logo" alt="logo" />
            <img src={fundoCentral} className="App-logo" alt="logo" />
            <div className='texto'>
                <p>Descubra o porqu nos escolher para projetar a sua piscina</p>
              </div>
              <img src={porqueGeralPC} className="PorqueGeralPC" alt="logo"/>
            <img src={porqueGeralMobile} className="PorqueGeralMobile" alt="logo"/>
              <div className='texto'>
                <p>Fundada por Tiago Augusto, profissional com mais de 20 anos de experincia na rea de construo e venda de piscinas, a LifeGold j produziu e vendeu mais de 300 piscinas. Acompanhamos voc do incio ao fim da construo, para que voc possa ter profissionais qualificados e certificados na rea, sempre  disposio para tornar seu sonho realidade</p>
              </div>


            </header>
            <div className ="App">

            </div>
            <TelaContato/>
        </div>
        
    );
}

export default Sobre;